@charset "utf-8";

// Define defaults for each variable.

$base-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$base-font-size:   1rem !default;
$base-font-weight: 400 !default;
$base-line-height: 1.5 !default;

@import url('https://fonts.googleapis.com/css?family=Monofett&display=swap');
$monofett: "Monofett", cursive;
.monofett {
  font-family: $monofett;
}

$spacing-unit:     2rem !default;

$mint-green: #efffe5;
$leaf-green: #78cc6e;
$charcoal: #444;
$black: #111;

$text-color:      $black !default;
$background-color: $mint-green;
$brand-color:      $leaf-green;

$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;

.color-mint-green {
  color: $mint-green;
}
.color-leaf-green {
  color: $leaf-green;
}
.color-grey {
  color: $grey-color;
}
.color-grey-light {
  color: $grey-color-light;
}
.color-grey-dark {
  color: $grey-color-dark;
}

$table-text-align: left !default;

// Width of the content area
$content-width:    832px !default;

@mixin relative-font-size($ratio) {
  font-size: $base-font-size * $ratio;
}


$breakpoints: (
  'sm': 640px,
  'md': 832px,
  'lg': 1024px,
  'xl': 1152px
) !default;



// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/syntax-highlighting",
  "minima/typography",
  "minima/DoubleImageComponent",
  "minima/Footer",
  "minima/ImageComponent",
  "minima/Post"
;


.flex {
  display: flex;
}
.md-flex {
  @media (min-width: map-get($breakpoints, md)) {
    display: flex;
  }
}
.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}
.flex-grow { flex-grow: 1; }
.flex-1 { flex-grow: 1; }
.flex-2 { flex-grow: 2; }
.flex-3 { flex-grow: 3; }
.flex-4 { flex-grow: 4; }
.justify-start {
  justify-content: flex-start;
}
.justify-end {
  justify-content: flex-end;
}
.justify-center {
  justify-content: center;
}
.align-start {
  align-items: flex-start;
}
.align-end {
  align-items: flex-end;
}
.align-center {
  align-items: center;
}

.md-w25 {
  @media (min-width: map-get($breakpoints, md)) {
    width: 25%;
  }
}
.md-w75 {
  @media (min-width: map-get($breakpoints, md)) {
    width: 75%;
  }
}

.debug { border: 1px solid orangered; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

.mt_5 {
  margin-top: 0.5rem;
}
.mt1 {
  margin-top: 1rem;
}
.mb_5 {
  margin-bottom: 0.5rem;
}
.mb1 {
  margin-bottom: 1rem;
}